import { useState, useEffect } from 'react'
import { supabase } from '@/lib/supabase'
import { useRouter } from 'next/router'
import { Plus, Edit2, Trash2, Save, X, Tag, ListOrdered, AlignLeft } from 'lucide-react'
import AdminLayout from '@/components/AdminLayout'

function CategoriesContent() {
  const router = useRouter()
  const [categories, setCategories] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [editingId, setEditingId] = useState<string | null>(null)
  const [newCategory, setNewCategory] = useState({ name: '', description: '', emoji: 'üç¶', sort_order: 0 })
  const [editForm, setEditForm] = useState({ name: '', description: '', emoji: '', sort_order: 0 })

  useEffect(() => {
    checkAuth()
    loadCategories()
  }, [])

  const checkAuth = async () => {
    const { data: { session } } = await supabase.auth.getSession()
    if (!session) {
      router.push('/auth/login')
    }
  }

  const loadCategories = async () => {
    setLoading(true)
    const { data } = await supabase
      .from('categories')
      .select('*')
      .order('sort_order', { ascending: true })
    
    if (data) setCategories(data)
    setLoading(false)
  }

  const handleCreate = async () => {
    if (!newCategory.name.trim()) return

    const { error } = await supabase
      .from('categories')
      .insert({
        name: newCategory.name,
        description: newCategory.description,
        emoji: newCategory.emoji,
        sort_order: newCategory.sort_order || categories.length
      })

    if (!error) {
      setNewCategory({ name: '', description: '', emoji: 'üç¶', sort_order: 0 })
      loadCategories()
    }
  }

  const handleEdit = (category: any) => {
    setEditingId(category.id)
    setEditForm({
      name: category.name,
      description: category.description || '',
      emoji: category.emoji || 'üç¶',
      sort_order: category.sort_order || 0
    })
  }

  const handleUpdate = async (id: string) => {
    const { error } = await supabase
      .from('categories')
      .update({
        name: editForm.name,
        description: editForm.description,
        emoji: editForm.emoji,
        sort_order: editForm.sort_order
      })
      .eq('id', id)

    if (!error) {
      setEditingId(null)
      loadCategories()
    }
  }

  const handleDelete = async (id: string, name: string) => {
    if (!confirm(`Kategorie "${name}" wirklich l√∂schen?`)) return
    await supabase.from('categories').delete().eq('id', id)
    loadCategories()
  }

  return (
    <div className="p-8 max-w-5xl mx-auto">
      {/* Header */}
      <div className="mb-10">
        <h1 className="text-4xl font-display font-bold italic text-[#4a5d54]">Kategorien</h1>
        <p className="text-[#8da399] italic">Strukturiere dein Sortiment</p>
      </div>

      <div className="grid lg:grid-cols-3 gap-10">
        {/* Links: Formular */}
        <div className="lg:col-span-1">
          <div className="bg-white rounded-[2rem] p-8 shadow-sm border border-gray-100 sticky top-8">
            <h2 className="text-xl font-display font-bold italic text-[#4a5d54] mb-6 flex items-center gap-2">
              <Plus size={20} /> Neu erstellen
            </h2>
            
            <div className="space-y-5">
              <div className="flex gap-3">
                <div className="w-20">
                  <label className="text-[10px] font-black uppercase text-[#8da399] ml-2 mb-1 block">Icon</label>
                  <input
                    type="text"
                    value={newCategory.emoji}
                    onChange={(e) => setNewCategory({ ...newCategory, emoji: e.target.value })}
                    className="w-full bg-[#f9f8f4] border-none rounded-2xl p-3 text-2xl text-center focus:ring-2 ring-[#4a5d54] outline-none"
                    maxLength={2}
                  />
                </div>
                <div className="flex-1">
                  <label className="text-[10px] font-black uppercase text-[#8da399] ml-2 mb-1 block">Name</label>
                  <input
                    type="text"
                    placeholder="Eisbecher..."
                    value={newCategory.name}
                    onChange={(e) => setNewCategory({ ...newCategory, name: e.target.value })}
                    className="w-full bg-[#f9f8f4] border-none rounded-2xl p-4 text-sm focus:ring-2 ring-[#4a5d54] outline-none"
                  />
                </div>
              </div>

              <div>
                <label className="text-[10px] font-black uppercase text-[#8da399] ml-2 mb-1 block">Beschreibung</label>
                <textarea
                  placeholder="Kurze Info f√ºr Kunden..."
                  value={newCategory.description}
                  onChange={(e) => setNewCategory({ ...newCategory, description: e.target.value })}
                  className="w-full bg-[#f9f8f4] border-none rounded-2xl p-4 text-sm focus:ring-2 ring-[#4a5d54] outline-none h-24 resize-none"
                />
              </div>

              <div>
                <label className="text-[10px] font-black uppercase text-[#8da399] ml-2 mb-1 block">Sortierung</label>
                <div className="relative">
                  <ListOrdered className="absolute left-4 top-1/2 -translate-y-1/2 text-[#8da399]" size={16} />
                  <input
                    type="number"
                    value={newCategory.sort_order}
                    onChange={(e) => setNewCategory({ ...newCategory, sort_order: parseInt(e.target.value) })}
                    className="w-full bg-[#f9f8f4] border-none rounded-2xl pl-12 pr-4 py-3 text-sm focus:ring-2 ring-[#4a5d54] outline-none"
                  />
                </div>
              </div>

              <button
                onClick={handleCreate}
                className="w-full bg-[#4a5d54] text-white rounded-2xl py-4 font-bold hover:scale-[1.02] active:scale-95 transition-all shadow-lg shadow-[#4a5d54]/20 mt-2"
              >
                Kategorie anlegen
              </button>
            </div>
          </div>
        </div>

        {/* Rechts: Liste */}
        <div className="lg:col-span-2">
          {loading ? (
            <div className="text-center py-20 animate-pulse text-[#8da399] italic">L√§dt Kategorien...</div>
          ) : categories.length === 0 ? (
            <div className="text-center py-20 bg-white rounded-[2rem] border-2 border-dashed border-gray-100">
              <Tag size={48} className="mx-auto text-gray-200 mb-4" />
              <p className="text-gray-400 font-medium">Noch keine Kategorien vorhanden</p>
            </div>
          ) : (
            <div className="space-y-4">
              {categories.map((cat: any) => (
                <div 
                  key={cat.id} 
                  className={`bg-white rounded-[2rem] p-6 shadow-sm border-2 transition-all duration-300 ${
                    editingId === cat.id ? 'border-[#4a5d54] ring-4 ring-[#4a5d54]/5' : 'border-transparent'
                  }`}
                >
                  {editingId === cat.id ? (
                    /* EDIT MODE */
                    <div className="space-y-4">
                      <div className="flex gap-4">
                        <input
                          type="text"
                          value={editForm.emoji}
                          onChange={(e) => setEditForm({ ...editForm, emoji: e.target.value })}
                          className="w-16 bg-[#f9f8f4] border-none rounded-xl p-2 text-xl text-center focus:ring-1 ring-[#4a5d54] outline-none"
                        />
                        <input
                          type="text"
                          value={editForm.name}
                          onChange={(e) => setEditForm({ ...editForm, name: e.target.value })}
                          className="flex-1 bg-[#f9f8f4] border-none rounded-xl px-4 py-2 text-sm focus:ring-1 ring-[#4a5d54] outline-none font-bold"
                        />
                        <input
                          type="number"
                          value={editForm.sort_order}
                          onChange={(e) => setEditForm({ ...editForm, sort_order: parseInt(e.target.value) })}
                          className="w-20 bg-[#f9f8f4] border-none rounded-xl px-2 py-2 text-sm text-center focus:ring-1 ring-[#4a5d54] outline-none"
                        />
                      </div>
                      <input
                        type="text"
                        value={editForm.description}
                        onChange={(e) => setEditForm({ ...editForm, description: e.target.value })}
                        className="w-full bg-[#f9f8f4] border-none rounded-xl px-4 py-3 text-sm focus:ring-1 ring-[#4a5d54] outline-none"
                        placeholder="Beschreibung..."
                      />
                      <div className="flex justify-end gap-2">
                        <button onClick={() => setEditingId(null)} className="px-4 py-2 text-gray-400 hover:text-gray-600 font-bold text-xs uppercase tracking-widest transition">
                          Abbrechen
                        </button>
                        <button 
                          onClick={() => handleUpdate(cat.id)}
                          className="bg-[#4a5d54] text-white px-6 py-2 rounded-xl font-bold text-xs uppercase tracking-widest flex items-center gap-2"
                        >
                          <Save size={14} /> Sichern
                        </button>
                      </div>
                    </div>
                  ) : (
                    /* VIEW MODE */
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-6">
                        <div className="w-16 h-16 bg-[#f9f8f4] rounded-2xl flex items-center justify-center text-3xl shadow-inner">
                          {cat.emoji || 'üç¶'}
                        </div>
                        <div>
                          <div className="flex items-center gap-3">
                            <h3 className="font-display font-bold text-xl text-[#4a5d54] italic">{cat.name}</h3>
                            <span className="bg-gray-100 text-[10px] font-black px-2 py-0.5 rounded text-gray-400 uppercase">Pos. {cat.sort_order}</span>
                          </div>
                          {cat.description && (
                            <p className="text-sm text-[#8da399] mt-1 line-clamp-1 italic">{cat.description}</p>
                          )}
                        </div>
                      </div>
                      
                      <div className="flex gap-1">
                        <button
                          onClick={() => handleEdit(cat)}
                          className="p-3 text-[#4a5d54] hover:bg-[#f9f8f4] rounded-2xl transition-colors"
                          title="Bearbeiten"
                        >
                          <Edit2 size={18} />
                        </button>
                        <button
                          onClick={() => handleDelete(cat.id, cat.name)}
                          className="p-3 text-red-300 hover:bg-red-50 hover:text-red-500 rounded-2xl transition-colors"
                          title="L√∂schen"
                        >
                          <Trash2 size={18} />
                        </button>
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

export default function CategoriesPage() {
  return (
    <AdminLayout>
      <CategoriesContent />
    </AdminLayout>
  )
}